
#news-page-header {
  width: 100%;
  margin: 6vh 0 6vh 0;
  text-align: center;
}

.news-container {
  overflow-y: auto;
}

.news-browser-post {
  display: block;
  margin: 2vh 16px;
  text-decoration: none;
  padding: 1vh 2vw 1vh 2vw;
  width: 95%;
  transition: all .25s;
  border: 1px solid grey;
  background-color: #131a4273;
}

.news-browser-post:hover {
  transform: scale(1.05);
  border: 1px solid white;
  filter: drop-shadow(1px 1px 1px rgba(155, 155, 155, .2));
}

.news-post-head {
  padding: 0 12px 0 12px;
}

.news-post-body {
  margin: 0;
  white-space: pre-wrap;
  padding: 8px 0 32px 0;
}

.news-post-head>h1 {
  text-align: center;
  font-size: calc(9px + 2vmin);
  color: rgba(255, 255, 255, .91);
}

.news-post-head>h2 {
  text-align: center;
  font-size: calc(8px + 1.85vmin);
  color: rgba(255, 255, 255, .81);
}

.news-post-footer>h3 {
  text-align: center;
  font-size: calc(6px + 1.5vmin);
  color: rgba(255, 255, 255, .66);
}

.item-sec {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  width: 75%;
  margin: 16px auto;
  padding: 0 16px 0 32px;
  text-align: left;
  border: 1px solid rgba(255,255,255,.366);
}

.sec-selected {
  background-color:rgba(155, 225, 155, 0.425);
}

.item-sec>h4 {
  user-select: none;
}

.item-sec>input {
  width: 18px;
  height: 18px;
  margin: auto 0 auto auto;
}

.item-sec:hover {
  background-color: rgba(150,0,0,.133);
}
